.sidebar-form {
	margin-top: var(--margins-top);
	margin-bottom: var(--margins-bottom);
	padding-top: var(--paddings-top);
	padding-bottom: var(--paddings-bottom);
	background: var(--gradient);

	@media (max-width: 1199.98px) {
		margin-top: var(--margins-top--mobile);
		margin-bottom: var(--margins-bottom--mobile);
		padding-top: var(--paddings-top--mobile);
		padding-bottom: var(--paddings-bottom--mobile);
	}

	// &__inner {
	// }

	&__title {
		font-weight: 300;
		font-size: 35px;
		line-height: 100%;
		color: #3a4454;
		margin-bottom: 24px;

		@media (max-width: 767.98px) {
			margin-bottom: 16px;
		}

		@media (max-width: 575.98px) {
			font-size: 30px;
		}
	}

	&__description {
		font-weight: 400;
		font-size: 16px;
		line-height: 150%;
		color: #556176;
		margin-bottom: 36px;

		@media (max-width: 767.98px) {
			margin-bottom: 30px;
		}
	}

	&__form {
		div.gform_wrapper.gravity-theme {
			border: none;
			border-radius: 0px;
			padding: 0px;
			grid-row-gap: 16px;
		}

		div.gform_wrapper.gravity-theme .gfield:not(.gfield_contains_required) .gfield_label::after {
			content: '(opcional)';
		}

		div.gform_wrapper.gravity-theme .ginput_container_fileupload {
			position: relative;
			background: #ffffff;
			border: 1px dashed #007953;
			border-radius: 5px;
			transition: 0.3s;
			height: 48px;

			&:hover {
				cursor: pointer;
				box-shadow: 0px 5px 15px rgba(15, 35, 52, 0.2);
				border-style: linear;
			}
		}

		div.gform_wrapper.gravity-theme .ginput_container_fileupload .gform_fileupload_rules {
			display: none;
		}


		div.gform_wrapper.gravity-theme .ginput_container_fileupload .validation_message {
			position: absolute;
			top: 0;
			height: 48px;
			border: none;
			margin-top: 0;
			z-index: 2;
			width: 100%;
			text-align: center;
			padding: 15px;
			pointer-events: none;
		}

		div.gform_wrapper.gravity-theme .ginput_container_fileupload .ginput_preview {
			margin: 13px 9px;

			strong {
				display: none;
			}
		}

		div.gform_wrapper.gravity-theme .ginput_container_fileupload .large {
			opacity: 0;
		}

		div.gform_wrapper.gravity-theme .ginput_container_fileupload + .gfield_description {
			position: relative;
			font-weight: 400;
			font-size: 16px;
			line-height: 100%;
			color: #007953;
			margin-top: -45px;
			margin-bottom: 12px;
			text-align: center;
			pointer-events: none;

			&::after {
				display: inline-block;
				content: '';
				background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 17 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_5829_12002)'%3E%3Cpath d='M12.5003 10.0001V12.0001H4.50033V10.0001H3.16699V12.0001C3.16699 12.7334 3.76699 13.3334 4.50033 13.3334H12.5003C13.2337 13.3334 13.8337 12.7334 13.8337 12.0001V10.0001H12.5003ZM5.16699 6.00008L6.10699 6.94008L7.83366 5.22008V10.6667H9.16699V5.22008L10.8937 6.94008L11.8337 6.00008L8.50033 2.66675L5.16699 6.00008Z' fill='%23007953'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_5829_12002'%3E%3Crect width='16' height='16' fill='white' transform='translate(0.5)'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E%0A");
				width: 16px;
				height: 16px;
				margin-bottom: -2px;
				margin-left: 6px;
			}
		}

		div.gform_wrapper.gravity-theme .gfield_checkbox .gchoice {
			display: flex;

			a {
				padding-bottom: 0px;
				border-bottom: 1px solid #219963;
				font-weight: 700;
				font-size: 16px;
				line-height: 150%;
				color: #219963;
				transition: 0.3s;

				&:hover {
					color: #007953;
					border-bottom-width: 2px;
					border-bottom-color: #007953;
				}

				&:active {
					color: #219963;
					border-bottom-color: #219963;
				}
			}
		}

		div.gform_wrapper.gravity-theme .gfield_checkbox input {
			height: 24px;
			width: 18px;
			border: 1px solid #a5adb4;
			border-radius: 3px;

			&:hover,
			&:focus {
				box-shadow: none !important;
			}
		}

		div.gform_wrapper.gravity-theme .gfield_checkbox label {
			margin-left: 12px;
			line-height: 22px;
			max-width: calc(100% - 40px);
		}

		div.gform_wrapper.gravity-theme .gfield_radio .gchoice label {
			margin-left: 10px;
		}

		div.gform_wrapper.gravity-theme .gfield_radio input {
			height: 24px;
			height: 24px;
			width: 18px;
			border: 1px solid #a5adb4;
			border-radius: 3px;

			&:hover,
			&:focus {
				box-shadow: none !important;
			}
		}
	}
}
